import React, {Component} from 'react';
import DashBoardNew from './DashBoardNew';

class DashBoardComponent extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }

    componentWillMount() {
        let IMEINumber=JSON.parse(localStorage.getItem("imeiNumber"));
        if(IMEINumber)
        {
            this.props.GetPumpReading(IMEINumber);
        }
        setInterval(() => {
            var IMEINub = JSON.parse(localStorage.getItem("imeiNumber"));
            this.props.GetPumpReading(IMEINub);        
        },30000);
      }
    
    render(){
        return(<div><DashBoardNew motorReaddingData={this.props.motorReaddingData} setMoterData={this.props.setMoterData} GetPumpReading={this.props.GetPumpReading}/></div>);
    }
}
export default DashBoardComponent;